html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 10em;
  color: red;
  margin: 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.bg-img {
  max-width: 100%;
  height: 100vh;
}

:export {
  headerMaxHeight: 6;
}

.header {
  position: fixed;
  background-color: rgb(195, 214, 47);
  z-index: 1001;
}

.header-logos {
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
  width: 100vw;
  padding: 0.5em 0 0.5em;
}

.header-logos img {
  max-height: var(--header-height, 6em);
  min-height: var(--header-height, 6em);
}

hr {
  margin: 0;
  height: 2px;
}